import "core-js/modules/es.array.push.js";
import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.find.js";
import "core-js/modules/es.iterator.for-each.js";
import "core-js/modules/es.iterator.map.js";
import "core-js/modules/web.url-search-params.delete.js";
import "core-js/modules/web.url-search-params.has.js";
import "core-js/modules/web.url-search-params.size.js";
import WaveSurfer from 'wavesurfer.js';
export default {
  data() {
    return {
      wavesurfer: null,
      isPlaying: false,
      duration: 0,
      currentTime: 0,
      list: [],
      dataObj: {},
      audioList: [],
      currentPlaying: null,
      stepIndex: 0,
      endCount: [],
      email: '',
      dialogFormVisible: false,
      form: {
        email: '',
        age: '',
        gender: ''
      }
    };
  },
  computed: {
    nextDisabled() {
      return this.audioList.length > this.endCount.length + 1;
    }
  },
  async mounted() {
    // 
    const response = await fetch('/score.json');
    this.list = await response.json();
    this.audioList = this.list[0].audios.map(item => ({
      name: item.name,
      url: item.url,
      disabled: true,
      score: 0
    }));
    this.audioList.forEach(audio => {
      new Audio(audio.url).preload = 'metadata';
    });
    this.initWavesurfer(this.audioList[0]);
    // email
    const res = await fetch('/email.json');
    const obj = await res.json();
    this.email = obj.email;
  },
  methods: {
    handleFileUpload(e) {
      const files = Array.from(e.target.files);
      this.audioList = files.map(file => ({
        name: file.name,
        url: URL.createObjectURL(file),
        disabled: true,
        score: 0
      }));
      this.initWavesurfer(this.audioList[0]);
    },
    testEmail() {
      // 1.  URL 
      const url = "http://112.74.105.167:8001/send-email";
      const data = {
        device_nickname: "user",
        email_to: "936507807@qq.com",
        detail: 'ceshi'
      };

      // 2.  Fetch 
      fetch(url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json" // 
        },
        body: JSON.stringify(data) //  JSON 
      }).then(response => {
        if (!response.ok) {
          throw new Error(`HTTP  ${response.status}`);
        }
        return response.json(); //  JSON 
      }).then(result => {
        console.log(":", result);
      }).catch(error => {
        console.error(":", error);
      });
    },
    initWavesurfer(audio) {
      this.wavesurfer = WaveSurfer.create({
        container: this.$refs.waveform,
        waveColor: '#718096',
        progressColor: '#4a5568',
        cursorColor: '#2d3748',
        height: 80,
        responsive: true,
        normalize: true
      });

      // 
      this.wavesurfer.load(audio.url);
      this.currentPlaying = audio.url;

      // 
      this.wavesurfer.on('ready', () => {
        this.duration = this.wavesurfer.getDuration();
      });
      this.wavesurfer.on('audioprocess', time => {
        this.currentTime = time;
      });
      this.wavesurfer.on('finish', () => {
        this.isPlaying = false;
        // const url = this.currentPlaying
        // const target = this.audioList.find(item => item.url === url)
        // if (target && target.disabled) {
        //   target.disabled = false
        //   this.endCount ++
        // }
      });
    },
    togglePlay(audio) {
      if (this.currentPlaying !== audio.url) {
        this.wavesurfer.destroy();
        // URL.revokeObjectURL(this.currentPlaying)
        this.initWavesurfer(audio);
        this.wavesurfer.playPause();
      } else {
        this.wavesurfer.playPause();
      }
      this.isPlaying = this.wavesurfer.isPlaying();
      if (this.isPlaying) {
        const target = this.audioList.find(item => item.url === url);
        if (target && target.disabled) {
          target.disabled = false;
        }
      }
    },
    formatTime(seconds) {
      return [Math.floor(seconds / 60), Math.floor(seconds % 60).toString().padStart(2, '0')].join(':');
    },
    handleScoreChange(audio) {
      if (!this.endCount.includes(audio.url)) {
        this.endCount.push(audio.url);
      }
    },
    handleNext() {
      const step = this.list[this.stepIndex].name;
      this.dataObj[step] = JSON.parse(JSON.stringify(this.audioList));
      if (this.stepIndex === this.list.length - 1) {
        this.dialogFormVisible = true;
        // console.log('send email')
        // this.sendEmail(this.dataObj)
      } else {
        this.currentTime = 0;
        this.wavesurfer.destroy();
        // URL.revokeObjectURL(this.currentPlaying)
        this.stepIndex += 1;
        this.currentPlaying = null;
        this.endCount = [];
        this.audioList = [];
        const i = this.stepIndex;
        this.audioList = this.list[i].audios.map(item => ({
          name: item.name,
          url: item.url,
          disabled: true,
          score: 0
        }));
        this.audioList.forEach(audio => {
          new Audio(audio.url).preload = 'metadata';
        });
        this.initWavesurfer(this.audioList[0]);
      }
    },
    readyToSendEmail() {
      this.dataObj['personalInfo'] = {
        ...this.form
      };
      this.sendEmail(this.dataObj);
      this.dialogFormVisible = false;
      this.$router.push('/home');
    },
    async sendEmail(data) {
      console.log(':', this.email);
      console.log(':', JSON.stringify(data, null, 2));
      const subject = `test`;
      const body = `
          : ${JSON.stringify(data, null, 2)}
        `;
      window.location.href = `mailto:${this.email}?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;
    }
  }
};